/* Position and sizing of burger button */
.bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 24px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #3333cc;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
    padding-left: 24px;
  }

  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #3333cc;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #3333cc;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: white;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  /* Menu link colors */
  a {
      color: #3333cc;
  }

  ol.venueList {
    padding: 0px;
}

#search{
  width: 100%;
  height: 1.5em;
}

.listItem {
  list-style: none;
  -webkit-box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
          box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

.attrib{
  font-size: 0.5em;
}


